<template>
  <v-card>
    <v-layout>
      <v-flex>
        <div class="headline"></div>
      </v-flex>
      <div>
        <v-btn fab dark small color="pink" :loading="loading" title="" @click="load">
          <v-icon dark>refresh</v-icon>
        </v-btn>
        <uploader :context="ctx" dir="" keep-name title="" @success="onAdd" accept="application/x-zip-compressed">
          <!-- <v-icon x-large dark>add</v-icon> -->
          <span class="pa-2">
            <v-icon x-large dark color="pink">add</v-icon>
          </span>
        </uploader>
      </div>
    </v-layout>
    <v-data-table v-if="items.length>0" :headers="headers" :items="items" hide-actions class="elevation-1">
      <template slot="items" slot-scope="props">
        <td class="text-xs-left">
          <v-icon>star</v-icon>
          <a @click="onClick(props.item)">{{ props.item.name }}</a>
        </td>
        <td>{{ props.item.ctime | date }}</td>
        <td>{{ props.item.mtime | date }}</td>
        <td class="text-xs-right">
          <v-btn color="primary" flat title="" @click="onClick(props.item)">
            
          </v-btn>
          <v-btn color="error" flat title="" @click="del(props.item.path)">
            <v-icon>delete</v-icon>
          </v-btn>
          <v-btn title="" flat :href="props.item.zipUrl" target="_blank">
            <v-icon>cloud_download</v-icon>
          </v-btn>
        </td>
      </template>
    </v-data-table>
    <v-alert :value="true" type="info" v-else>
      
    </v-alert>
  </v-card>
</template>

<script>
import { list, zip } from '@/api/product'
import { delFile } from '@/api/common'
import Uploader from '@/components/Uploader'
export default {
  components: {
    Uploader
  },
  data() {
    return {
      headers: [
        { text: '', value: 'name' },
        { text: '', value: 'mtime' },
        { text: '', value: 'ctime' },
        { text: '', align: 'center', sortable: false }
      ],
      items: [],
      ctx: '',
      loading: true
    }
  },
  created() {
    this.load()
  },
  methods: {
    load() {
      this.loading = true
      list().then(({ ctx, items }) => {
        this.ctx = ctx
        this.items = items.sort((prev, next) => next.mtime - prev.mtime)
        this.loading = false
      })
    },
    async onAdd(path) {
      var data = await zip(path)
      this.items.push(data)
      await this.load()
      this.$toast('')
    },
    onClick(item) {
      this.$router.push({
        path: '/product/browser',
        query: {
          path: item.path
        }
      })
    },
    async del(path) {
      try {
        await this.$confirm('')
      } catch (e) {
        return
      }
      await delFile(path)
      this.$toast('')
      await this.load()
    }
  }
}
</script>

<style>
</style>



